var Vue      = require('vue');
var VueResource = require('../lib/vue-resource');
var env    = require('./env');
var wx = require('weixin-js-sdk');
var utility = require('./utility');

Vue.http.get(env.api + 'wechat/sign',{
    params : {
        url : encodeURIComponent(window.location.href)
    }
}).then(function(res){
    wx.config({
        debug: false, // ,apialertpclogpc
        appId: res.body.data.appid, // 
        timestamp: res.body.data.timestamp, // 
        nonceStr: res.body.data.noncestr, // 
        signature: res.body.data.signature,// 1
        jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage'] // JSJS2
    });


    linkUrl = 'https://' + window.location.host + '/product.html?id=' + utility.getUrlParam('id') + '&shareId=' + (utility.getUrlParam('shareId') ? localStorage['share_openid'] : localStorage['openId'])


    var datas = {
        title: '', // 
        link: linkUrl, // 
        imgUrl: '', // 
        success: function () { 
           
        },
        cancel: function () { 

        }
    }
    wx.ready(function(){
        wx.onMenuShareAppMessage(datas);
        wx.onMenuShareTimeline(datas);
    });
},function(){

})



