module.exports = {
    get : function (url,fn){
        var obj=new XMLHttpRequest();  // XMLHttpRequest          
        obj.open('GET',url,false);
        obj.onreadystatechange=function(){
            if (obj.readyState == 4 && obj.status == 200 || obj.status == 304) { // readyState==4
                fn.call(this, obj.responseText);  //
            }
        };
        obj.send(null);
    }
}


